}

/*********************************/
/*    Header, Footer, Article    */
/*********************************/
.gw-header,
.gw-footer {
  padding: 12px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: horizontal;
          flex-direction: horizontal;
}
.gw-header {
  margin-bottom: 30px;
}
.gw-footer {
