/*********************************/
/*             Forms             */
/*********************************/
.gw-form.gw-field-padding .gw-field-group {
  margin-bottom: 10px;
}
.gw-inline-form.gw-field-padding .gw-field-group {
  margin-right: 10px;
}

/** outer wrapper for fields */
.gw-field {
  margin: 8px;
  padding-bottom: 16px;
  background-color: inherit;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.gw-field .gw-field-feedback {
  margin-left: 8px;
  height: 14px;
}
input[readonly] {
  border: none;
}
.gw-field.label-left {
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.gw-field.label-left .gw-field-body {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
}

/** custom input field wrapper */
.gw-input {
  -webkit-transition: border-color 0.2s ease;
  transition: border-color 0.2s ease;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: inherit;
  cursor: text;
}

/** real input field */
.gw-input input {
  padding: 0px 8px 4px 8px;
  height: 25px;
  outline: none;
  border: none;
  border-bottom: 1px solid lightslategray;
  width: 100%;
  margin-top: 1px;
  font-size: 14px;
  background-color: inherit;
  -webkit-transition: color 0.3s ease;
  transition: color 0.3s ease;
}

/** add full border to input */
.gw-field.border input {
  border: 1px solid lightslategray;
  border-radius: 3px;
  margin-top: 0px;
  padding-top: 3px;
}
.gw-field.border input,
.gw-field.border.label-top input {
  margin-top: 6px;
}

/** has-focus and has-value handler for input */
.gw-field.has-focus input,
.gw-field.has-value input {
  color: black;
}

/** field label */
.gw-field label {
  margin-left: 6px;
  font-size: 13px;
  color: gray;
  font-weight: bold;
  background-color: inherit;
  padding-left: 2px;
  padding-right: 2px;
}

/** field border label */
.gw-field.border label {
  margin-top: 19px;
}
.gw-field.border.label-slide {
  padding-top: 6px;
}

/** slide label */
.gw-field.label-slide label {
  position: absolute;
  margin-top: 22px;
  cursor: text;
  -webkit-transition: font-size 0.24s ease, margin-top 0.2s ease;
  transition: font-size 0.24s ease, margin-top 0.2s ease;
  pointer-events: none;
}

/** slide label border */
.gw-field.border.label-slide label {
  margin-top: 13px;
}
.gw-field.border.label-slide input {
  padding-top: 7px;
}

/** slide label has-focus/has-value */
.gw-field.label-slide.has-focus label,
.gw-field.label-slide.has-value label {
  margin-top: 1px;
}

/** slide label border has-focus/has-value */
.gw-field.border.label-slide.has-focus label,
.gw-field.border.label-slide.has-value label {
  margin-top: -5px;
}
.gw-field.border.label-top label {
  margin-top: 0px;
}
.gw-field.label-left input {
  margin-top: 0px;
}

/** default left label width */
.gw-field.label-left label {
  width: 150px;
}
.gw-field.label-left label,
.gw-field.label-left.has-focus label,
.gw-field.label-left.has-value label {
  margin-top: 7px;
}
.gw-field.border.label-left label,
.gw-field.border.label-left.has-focus label,
.gw-field.border.label-left.has-value label {
  margin-top: 11px;
}
.gw-field.has-focus label {
  color: #0047c1;
}
.gw-field.no-error .gw-field-feedback {
  color: #3c763d;
}
.gw-field.has-error .gw-field-feedback {
  color: #a94442;
}
.gw-field .gw-field-feedback {
  font-size: 13px;
  margin-top: 2px;
  margin-left: 8px;
  height: 14px;
}
.gw-field.dirty.touched.invalid {
  padding-bottom: 0px;
}
.gw-field.has-focus .gw-input input {
  /* 	border-color: #5cb3fd; */
  border-color: #0047c1;
}
.gw-field.dirty.invalid.touched .gw-input input {
  /* 	border-color: #a94442; */
  border-color: #c10000;
}
.gw-field.dirty.invalid.touched label,
.gw-field.dirty.invalid.touched .gw-field-feedback {
  /* 	color: #a94442; */
  color: #c10000;
}
.gw-field.dirty.touched.valid .gw-input input {
  /* 	border-color: #00c139; */
  border-color: #01ad33;
}
.gw-field.dirty.touched.valid label {
  /* 	color: #00c139; */
  color: #01ad33;
}
.gw-field.no-error .gw-input {
  /* 	border-color: #3c763d; */
}
.gw-checkbox {
  display: -webkit-box;
